              }
          
              p {
                font-size: 1rem;
                line-height: 1.6;
                color: rgba(255, 255, 255, 0.972);
              }
                           
            }
          

            .header{
              position: relative; /* Needed for positioning overlay */
              background-image: url("women-model.jpg");
              background-size: cover; 
              background-repeat: no-repeat;
              background-position: center center;
              height: 95vh;
              align-items: flex-start;
              justify-content: center;
              

              h2{
                text-align: left;
                font-size: 3.5rem;
              }
              p{
                text-align: justify;
              }
              .overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.717);
