              }
        
              
              
            }
            @media (max-width: 462px) {
                
              .gallery-section{
                height: 110vh;
              .image-container {
               flex-direction: column;
               justify-content: center;
               align-items: center;
                .image1, .image2, .image3 {
                  width: 300px;
                  height: 180px; 

                  
                }
              }
              .image-container.passive{
                opacity: 0;
              }

            }}

          

          .joinus-section{
              height: 90vh;
              background-image: url("joinUs.jpg");
              background-size:cover;
              background-repeat: no-repeat;
