            height: 100%;
            background-color: rgba(0, 0, 0, 0.7); /* Overlay color */
            mix-blend-mode: multiply; /* Blend mode to darken */
          }

          @media (max-width: 500px) {
            .header .content, .about-section, .gallery-section, .joinus-section{
              padding-left: $mobPadding;
