    // ADMIN NAV (left)
    .admin-nav{
        z-index: 2;
        width: 250px;
        height: 100vh;
        background-color: teal;
        color: #fff;
        position: fixed;
        top: 0;
        left: 0;
        overflow-y: auto;

        .nav-header {
            padding: 20px;
            text-align: center;
            margin-top: 25px;
            margin-bottom: 30px;
          }
          
          .nav-list {
            list-style-type: none;
            padding: 0;
          }
          
          .nav-item > a {
            display: block;
            padding: 12px 20px;
            padding-left: 32px;
            color: #fff;
            text-decoration: none;
            transition: background-color 0.3s;
          }
          
          .nav-item > a:hover {
            background-color: rgb(37, 161, 161);
            cursor: pointer;
          }
          
          .dropdown {
            display: none;
            background-color: rgb(34, 155, 155);
            list-style-type: none;
            padding: 0;
          }
          
          .nav-item .dropdown.active{
            display: block;
          }
          
          .dropdown li > a {
            display: block;
            padding: 10px 60px;
            color: #fff;
            text-decoration: none;
            transition: background-color 0.3s;
          }
          
          .dropdown li > a:hover {
            background-color: rgb(39, 141, 141);
          }

          /* Style for the logout button */
            .logout-button {
                position: relative;
                margin-top: 90px;
                bottom: 10px;
                left: 43%;
                background-color: transparent;
                border: none;
                color: #fff;
                cursor: pointer;
                font-size: 20px;
                height:26px;
                width: 26px;
                padding: 2px;
                padding-left: 3px;
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .nav-item {
              a{
                display: flex;
                align-items: center;
                gap: 6px;
              }
            }
            
            
            

            .close{
              position: absolute;
              top: 15px;
              left: 205px;
              font-size: 26px;
              height:26px;
              width: 26px;
              padding: 2px;
              cursor: pointer;
            }
            .close:hover, .logout-button:hover{
              border-radius: 15px;
              background-color: rgb(37, 161, 161);
            }
            animation: slideIn 0.3s ease-out forwards;
    }

    @keyframes slideOut {
      from {
        transform: translateX(0); /* Starting position */
        // opacity: 1; /* Starting opacity */
      }
      to {
        transform: translateX(-100%); /* Ending position */
        // opacity: 0; /* Ending opacity */
      }
    }

    @keyframes slideIn {
      from {
        transform: translateX(-100%); 
        
      }
      to {
        transform: translateX(0); 
        
        
      }
    }

    /* For the components in the admin page*/
    @keyframes slideOut2 {
      from {
        margin-left: 245px;
      }
      to {
        margin-left: 0px;
      }
    }

    @keyframes slideIn2 {
      from {
        margin-left: 0px;
      }
      to {
        margin-left: 250px;
      }
    }

            .admin-nav.closed{
              animation: slideOut 0.3s ease forwards;
              /* Move the element to the left and out of view */
            }

