              // padding: 2px;
              border-radius: 13px;
            }
          }

          .admin.closed, .edit-home.closed{
            animation: slideOut2 0.3s ease forwards;
          }
          
          .helper{
            position: absolute;
            left: 0;
            top: 90px;
            z-index: 1;
            button{
              border-top-left-radius: 0;
              border-bottom-left-radius: 0;
              border-top-right-radius: 12px;
              border-bottom-right-radius: 12px;
              background-color: teal;
              color: white;
              font-size: 15px;
              // transition: width 0.5s ease;
              p{
                opacity:0;
                display: none;}
              overflow: hidden; /* Hide overflow during animation*/
            }

            @keyframes fadeIn {
              from {
                width: 135px;
                height: 50px;
              }
              to {
                width: 170px;
                height: 50px;
              }
            }

            @keyframes fadeInP {
              from {
                opacity: 0;
              }
              to {
                opacity: 1;
              }
            }

            

            button:hover{
              animation: fadeIn 0.5s ease forwards;
              width: 160px;
              height: 50px;
              p{display: flex;
                animation: fadeInP 0.5s ease forwards;
              margin: 0;}
              .icon{
                animation: fadeInP 0.5s ease forwards;
                position: absolute;
                right: 0;
                top: 25%;
                margin-right: 20px;
                height: 30px;
              }
            }
          }

          .edit-home{
            position: absolute;
            left: 0px;
            top: 70px;
            right: 0px;
            bottom: 0px;
          }

          .navbar{
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
          }
          /*  To make the admin nav slide out when the screen is less than or equal to 690*/
          @media (max-width: 690px) {
            .admin-nav{
              animation: slideOut 0.3s ease forwards;
            }
            .admin, .edit-home{
              animation: slideOut2 0.3s ease forwards;
            }

            .admin-nav.closed{
              animation: slideIn 0.3s ease-out forwards;
            }
            
          }
          // @media (min-width: 691px) {
          //   .admin-nav{
          //     animation: slideIn 0.3s ease-out forwards;
          //   }            
          // }
    }

}